
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.left-enter{
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.left-enter-active,.left-leave-active{
  -webkit-transition:1s;
  transition:1s;
}
.left-leave-to{
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.right-enter{
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.right-enter-active,.right-leave-active{
  -webkit-transition:1s;
  transition:1s;
}
.right-leave-to{
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}






































































































































































































































































































































































































































































































































.v-enter{
  opacity: 0;
}
.v-enter-to{
  opacity: 1;
}
.v-enter-active,.v-leave-active{
  -webkit-transition: 1s;
  transition: 1s;
}
.v-leave{
  opacity: 1;
}
.v-leave-to{
  opacity: 0;
}

.v-enter{
  opacity: 0;
}
.v-enter-to{
  opacity: 1;
}
.v-enter-active,.v-leave-active{
  -webkit-transition: 1s;
  transition: 1s;
}
.v-leave{
  opacity: 1;
}
.v-leave-to{
  opacity: 0;
}












































































































































































































































































































































































































































































































































































































































































@charset "utf-8";
/* CSS Document */
html, body, ul, li, ol, dl, dd, dt, p, h1, h2, h3, h4, h5, h6, form, fieldset, legend, img,input{
	margin:0;padding:0;
}
body{
	font:12px/19px Arial, Helvetica, sans-serif,"\5B8B\4F53";
}
fieldset,img{
	border:0;
}
ul,ol{
	list-style:none;}
img { 
	border:none;
	display:block;
}
a {
	text-decoration:none;
}
/*=========去除默认样式*/
input[type="button"], input[type="submit"], input[type="reset"] {
-webkit-appearance: none;
}
textarea {  -webkit-appearance: none;}
.button{ border-radius: 0; }
input:focus,input:visited,input:hover{
    -webkit-tap-highlight-color:rgba(0,0,0,0);
    -webkit-user-modify:read-write-plaintext-only;
    outline:none;
}
a,.tape3-top2,.tape3-top3 div{
	-webkit-tap-highlight-color:rgba(0,0,0,0); 
	-moz-tap-highlight-color:rgba(0,0,0,0);

}
input,input:focus,textarea:focus ,a:focus{
    outline: none;
 
}
input::-ms-clear {
    display: none;
}
textarea {
    resize: none;
}
.index-content:before {
	content: ".";
	display: block;
	height: 0px;
	clear: both;
	visibility: hidden;
}
.index-content{
	display:inline-block;
	*display:inline;
	*zoom:1;
}
p{
	word-break:break-all;
}
.index-nav{
	width:100%;
	height:88px;
	background: #42B983;
}
.index-nav li{
	width:120px;
	height:88px;
	color:#fff;
	font-size: 18px;
	line-height: 96px;
	float:left;
}
.index-nav li a{
	display: block;
	width:120px;
	height:88px;
	color:#fff;
	font-size: 18px;
	line-height: 88px;
}
.index-nav li .router-link-active{
	background:#2C3E50;
	
}
.document-main,.document-ul{
	width:100%;
	height:48px;
	
}
.document-ul{
	border:1px solid #42B983;
	border-top:none;
}
.document-ul li{
	width:120px;
	height:48px;
	float:left;
}
.document-ul li a{
	width:120px;
	height:48px;
	text-align: center;
	line-height: 48px;
	display: block;
	font-size: 16px;
	color:#000;
}
.document-ul .document-ul-active a,.document-ul-active{
	background:black;
	color: #fff;
}
.html-main{
	width:100%;
	height:auto;
	
}
.html-main div{
	width:100%;
	height:auto;
	background: #eee;
	border:1px solid #aaa;
	margin-top:8px;
	padding:10px 0;
}
.html-main h3,.html-main h2,.html-main p{
	width:96%;
	height:auto;
	padding-left:2%;
	padding-right:2%;
	text-align: left;
	color:#888;
	
}
.html-main h3{
	font-size: 18px;
}
.html-main p:nth-of-type(1){
	font-size: 16px;
}
.index{
	width:100%;
	height:auto;
}
.index-main{
	width:96%;
	height:auto;
	margin-top:8px;
	padding:8px 2%;
	background: #eee;
}
.index-main-left{
	width:120px;
	height:auto;
	float:left;
}
.index-main-left img{
	display: block;
	width:108px;
	height:auto;
	border-radius: 50%;
	border:6px solid #fff;
}
.index-main-right{
	width:calc(100% - 120px);
	height:auto;
	float:left;
}
.index-main-right p,.index-main-right h2{
	width:98%;
	height:auto;	
	text-align: left;
	padding-left:2%;
}
.index-main-right p{
	font-size: 15px;
	font-weight: 900;
	padding-top:5px;
}
.about-main{
	width:100%;
	height:auto;
	margin-top:8px;
}
.about-main h2,
.about-main p{
	display: block;
	padding-left:200px;
	width:calc(100%-300px);
	height:auto;
	padding-top:8px;
	text-align: left;
	padding-right:100px;
}
.about-main p{
	font-size: 16px;
	font-weight: 900;
	line-height: 24px;

}
.about-main h2{
	line-height: 30px;
}
.center{
	width:100%;
	height:100%;
	position: absolute;
	left:0;
	top:98px;
	overflow: hidden;
}
html,body{
	overflow:hidden;
}
#but,#but1{
	width:100%;
 	height:auto;
 	background:#2C3E50;
	overflow:hidden;
	
}
#but img,#but1 img{
 	display:block;
 	width:100px;
 	height:100px;
 	float:left;
	cursor:pointer;
 	margin:0 8px;
 	border-radius:50%;
	-webkit-border-radius:50%;
}
#but1 img{
	opacity :0.3;
	-webkit-transition:width 1.5s, opacity 1.5s, height 1.5s;
	transition:width 1.5s, opacity 1.5s, height 1.5s;
}
#but1 img:hover{
	width:200px;
	height:200px;
	opacity: 1;
}
#but img:hover{
	-webkit-transition:.5s;
	transition:.5s;
	transform: rotate(90deg);
	-ms-transform: rotate(90deg);		
	-webkit-transform: rotate(90deg);	
	-o-transform: rotate(90deg);	
	-moz-transform: rotate(90deg);
}
#but h2,#but1 h2{
 	width:90%;
 	height:auto;
 	padding:15px 5%;
 	color:#fff;
 	text-align: left;

 }
.transtion-h2{
 	width:90%;
 	height:auto;
 	padding:15px 5%;
 	color:#fff;
 	text-align: left;
 	background: #2C3E50;
 }
#ul{
	width:330px;
	margin:20px 5% 0;
	height:340px;
}
#ul li{
	list-style:none;
	width:100px;
	height:100px;
	margin-right:10px;
	margin-top:10px;
	background:#42B983;
	float:left;
	text-align:center;
	color:#fff;
	line-height:200px;
	background-size:100% 100%;
	font-weight:900;
	border-radius:50%;
	-webkit-border-radius:50%;
	position: relative;	
}
#ul li img{
	display: none;
	position:absolute;
	top:0;
	left:0;
	border-radius:50%;
	-webkit-border-radius:50%;
	width:100px;
	height:100px;
	z-index:99;
	-webkit-transition:width 1s, height 1s, opacity 3s, -webkit-transform 1s;
	transition:width 1s, height 1s, opacity 3s, -webkit-transform 1s;
	transition:width 1s, height 1s, transform 1s,opacity 3s;
	transition:width 1s, height 1s, transform 1s,opacity 3s, -webkit-transform 1s;
	-webkit-transform-origin:50px 50px;
	        transform-origin:50px 50px;
}
#ul li img:hover{
	width:120px;
	height:120px;
	-webkit-transform:rotate(90deg);
	        transform:rotate(90deg);
	-webkit-transform-origin:55px 55px;
	        transform-origin:55px 55px;
	opacity: 0;
}
#ul li span{
	display: none;
	width:100%;
	height:auto;
	border-radius:50%;
	-webkit-border-radius:50%;
	z-index:999;
	text-align: center;
	line-height: 100px;
	font-size:22px;
	color:#fff;
}
#ul li .transition-show{
	display:block;
	
}
.transition-umain{
	width:100%;
	height:100%;
}
.main-center{
	width:100%;
	height:100%;
	overflow-y:auto;
}
.warp{
		margin:8px 50px;
		width:360px;
		height:360px;
		background:#42B983;
		border:10px solid #069;
		overflow:hidden;
		position:relative;
		border-radius: 50%;
		float:left;
	}
#warp-in{
		width:20000px;
		height:360px;
		position:absolute;
		left:0;
		top:0;
	}
#warp-in li{
		list-style:none;
		width:360px;
		height:360px;
		float:left;
		overflow: hidden;
	}
#warp-in li img{
		width:360px;
		height:360px;
		border-radius: 50%;
	}
#cl{background:#2C3E50;padding:0;border-radius: 50%;}